<script setup lang="ts">
    import { computed } from "@vue/reactivity";
    import { ref, type Ref, type VNodeRef } from "vue";
    import dayjs, { Dayjs } from "dayjs";

    const ageRangeMax = ref(dayjs("2022-07-01").add(-106, "day"));
    const ageRangeMin = ref(dayjs("2010-01-01").add(-106, "day"));

    // .add(-1911, 'year') 民國
    const y1 = ref(ageRangeMin.value.format("YYYY年MM月DD日"));
    const y2 = ref(ageRangeMax.value.format("YYYY年MM月DD日"));
    const y3 = ref(ageRangeMin.value.format("YYYY年MM月DD日"));
    const y4 = ref(ageRangeMax.value.format("YYYY年MM月DD日"));
    const y5 = ref(ageRangeMax.value.format("YYYY年MM月DD日"));

    const kidBirthdayStr: Ref<string> = ref("");

    const kidBirthdayWarning = ref(false);
    const kidBirthdayShowFrom = ref(false);
    const diffday = computed(() => Math.floor(dayjs().diff(kidBirthdayStr.value) / (1000 * 60 * 60 * 24)));

    function kidBirthdayClick() {
        const kidBirthday = dayjs(kidBirthdayStr.value);
        //console.log(kidBirthday)
        if (kidBirthday.isAfter(ageRangeMin.value) && kidBirthday.isBefore(ageRangeMax.value)) {
            kidBirthdayWarning.value = false;
            kidBirthdayShowFrom.value = true;
        } else {
            kidBirthdayWarning.value = true;
            kidBirthdayShowFrom.value = false;
        }
    }

    const emit = defineEmits<{
        (e: "kidBirthdayChange", kidBirthday: Dayjs): void;
    }>();



</script>
<template>
    <h1>兒童早療報名系統</h1>

    正確年齡計算方式：<br />
    「實足年齡」：「施測日期 - 出生日期 = 實足年齡」<br />
    ＊早產兒二歲前建議以預產期代替出生日期計算<br />

    <div class="kidBirthdayFrom">
        請選擇您的寶貝出生日期進行檢測：<br />
        <label class="form-label">
            <input type="date" class="form-control" v-model="kidBirthdayStr" required />
        </label>
        <!-- 
        <label class="form-label">
            <input type="button" class="btn btn-primary" style="vertical-align: inherit" value="確認" :onclick="kidBirthdayClick" />
        </label>
        -->
        <br />
        試算結果：{{ diffday >= 0 ? Math.floor(diffday / 30) + "個月" + Math.floor(diffday % 30) + "天" : "" }}
    </div>
    <div class="col-md-12" v-if="kidBirthdayWarning">
        <div class="alert alert-info">
        <!--
        本年度符合收案區間的年齡為：{{ y1 }}至{{ y2 }}（每一年都會調整）。<br>
        ‧出生日期為{{ y3 }}-{{ y4 }}，若媒合到適合就診日期為{{ y5 }}，則不符合收案年齡。<br>
        -->
            年齡不符合本院看診條件，若仍需要本院就診，建議轉診至以下科別：<br />
            動作、語言類發展問題 &#8594; 請掛復健科<br />
            認知、人際、情緒、專注力、過動等 &#8594; 請掛兒童心智科
        </div>
    </div>

    <br />
</template>
