<script setup lang="ts">
  import dayjs from "dayjs";
  import { computed, ref, type Ref } from "vue";

  const props = defineProps<{
    kidBirthday: string;
  }>();

  type monthRootT = {
    title: string;
    min: number;
    max: number;
  };
  type questionT = {
    title: string;
    special: boolean;
    answer: boolean;
    image: string;
  };
  type monthQuestionsT = {
    questions: questionT[];
    image: string;
  };
  const monthRoots: Ref<Record<string, monthRootT>> = ref({});
  const monthQuestions: Ref<monthQuestionsT> = ref({
    questions: [],
    image: "",
  } as monthQuestionsT);

  fetch("assets/monthRoot.json")
    .then((value) => value.json())
    .then((value) => {
      monthRoots.value = value;
    });

  const monthFile = ref("");
  const monthQuestionTitle = computed(() => {
    //console.log(props.kidBirthday);
    const diff = dayjs().diff(dayjs(props.kidBirthday));
    const diffday = diff / (1000 * 60 * 60 * 24);

    let title = "";
    for (const i in monthRoots.value) {
      //console.log(diffday);
      if (monthRoots.value[i].min <= diffday && monthRoots.value[i].max >= diffday) {
        monthFile.value = i;
        title = monthRoots.value[i].title;
        fetch(i)
          .then((value) => value.json())
          .then((value: monthQuestionsT) => {
            //console.log(value);
            value.questions.forEach((v, i, a) => {
              v.title = v.title.replace(/^\d+\./gm, "");
            });
            monthQuestions.value = value;
          });
        break;
      }
    }
    //console.log(title);
    return title;
  });
</script>
<template>
  <h5>5、TAIPEI II 篩檢表</h5>
  <div class="col-md-12">
    <div v-if="!monthQuestionTitle">
      <h6>請輸入出生日期/日期不在篩檢表範圍</h6>
    </div>
    <div v-if="monthQuestionTitle">
      <h6>{{ monthQuestionTitle }}</h6>
      <div>
        <div v-for="(v, i) in monthQuestions.questions" :class="i % 2 == 0 ? 'q_white_line' : 'q_gray_line'">
          <div class="row">
            <div class="col">
              {{ i + 1 }}<span style="color: red">{{ v.special ? "&#9733;" : "" }}</span>
            </div>
            <div class="col-md-9">
              <label class="form-check-label">{{ v.title }}</label>
              <img v-if="v.image" :src="v.image" height="64" />
            </div>
            
            <div class="col-md-2 text-center">
              <input type="radio" class="slist_radio_input" :id="`q5t_${i}`" :name="`${monthFile}_${i}`" required :value="true" />
              <label class="slist_radio_label" :for="`q5t_${i}`">是</label>

              <input type="radio" class="slist_radio_input" :id="`q5f_${i}`" :name="`${monthFile}_${i}`" required :value="false" />
              <label class="slist_radio_label" :for="`q5f_${i}`">否</label>
            </div>
            
          </div>
        </div>
        <img :src="monthQuestions.image" style="width: 100%" />
      </div>
    </div>
  </div>
  <br />
</template>

<style>
  .q_gray_line {
    background-color: #f0f0f0;
    padding: 5px;
  }

  .q_white_line {
    background-color: white;
    padding: 5px;
  }

  .slist_radio_input {
    display: inline-block;
    position: absolute;
    opacity: 0;
  }

  .slist_radio_label {
    text-align: center;
    background-color: #dee2e6;
    border-radius: 50%;
    cursor: pointer;
    position: relative;
    width: 2.5rem;
    height: 2.5rem;
    margin: 0.1rem;
    line-height: 2.5rem;
  }

  .slist_radio_input:checked + label {
    -webkit-box-shadow: inset 0 0 0 2px #000000;
    box-shadow: inset 0 0 0 2px #000000;
  }
</style>
