<script setup lang="ts">
    import dayjs, { Dayjs } from "dayjs";
    import { computed, ref, type Ref } from "vue";
    import SimpleFrom1 from "../components/SimpleFrom1.vue";
    import SimpleFrom2 from "../components/SimpleFrom2.vue";
    import SimpleFrom3 from "../components/SimpleFrom3.vue";
    import SimpleFrom4 from "../components/SimpleFrom4.vue";
    import SimpleFrom5 from "../components/SimpleFrom5.vue";
    import ReadRegulations from "@/components/ReadRegulations.vue";
    import { getDatabase, ref as fb_ref, push } from "firebase/database";
    import { useRouter } from "vue-router";
    import SimpleFrom0 from "../components/SimpleFrom0.vue";

    const allData: Record<string, any> = {};
    function formSubmit(payload: Event) {
        const formData = new FormData(payload.target as HTMLFormElement);
        for (const key of formData.keys()) {
            const saftKey = key.replace(/[\./]/gm, "_");
            allData[saftKey] = formData.getAll(key).join("|");
        }
        console.log(allData);

        step.value += 1;
        if (step.value > 5) {
            pushUserData(allData);
            return;
        }
    }

    const router = useRouter();
    function pushUserData(data: Record<string, any>) {
        data["unix_timestamp"] = dayjs().unix();

        const db = getDatabase();
        push(fb_ref(db, "register/"), {
            jsondata: data,
        })
            .then((value) => {
                alert(JSON.stringify(data, null, 2));
                router.push({ path: "submited" });
            })
            .catch((reason) => {
                console.error(reason);
            });
    }

    const kidBirthdayStr: Ref<string> = ref("");
    const step = ref(0);

    function kidBirthdayChange(kidBirthday: Dayjs) {
        kidBirthdayStr.value = kidBirthday.toString();
    }

</script>
<template>
    <main>
        <br />
        <ReadRegulations />
        <form id="form" @submit.prevent="formSubmit">
            <br />
            <SimpleFrom0 v-if="step == 0" @kid-birthday-change="kidBirthdayChange" />
            <SimpleFrom1 v-if="step == 1" />
            <SimpleFrom2 v-if="step == 2" />
            <SimpleFrom3 v-if="step == 3" />
            <SimpleFrom4 v-if="step == 4" />
            <SimpleFrom5 v-if="step == 5" :kid-birthday="kidBirthdayStr" />

            <button type="submit" class="btn btn-primary">確認</button>
        </form>

    </main>
</template>
<style>
    .kidBirthdayFrom {
        padding: 15px;
        background-color: #f0f0f0;
    }
</style>
