<script setup lang="ts">
import { ref, type Ref } from 'vue';


const isSpecialDisease :Ref<boolean | null> = ref(null)

const specialDiseaseList = [
  "神經系統",
  "內分泌",
  "罕見疾病",
  "早產"
];
</script>
<template>
    <h5>3、是否有特殊疾病？</h5>
    <div class="col-md-12">
      <div class="form-check">
        <label class="form-check-label">
          <input type="radio" class="form-check-input" name="isSpecialDisease" :value="false" v-model="isSpecialDisease"
            required>否
        </label>
      </div>
      <div class="form-check">
        <label class="form-check-label">
          <input type="radio" class="form-check-input" name="isSpecialDisease" :value="true" v-model="isSpecialDisease"
            required>是
        </label>
        <div v-if="isSpecialDisease">
          <div class="form-check form-check-inline" v-for="v, i in specialDiseaseList">
            <label class="form-check-label">
            <input type="checkbox" class="form-check-input" name="specialDisease" :value="v" :disabled="!isSpecialDisease">{{v}}
            </label>
          </div>
        </div>
      </div>
    </div>
    <br />

</template>